body{margin:0;color:#2d2d2d;font-family:"Roboto",sans-serif;font-size:16px;background-color:#fbfbfb;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;user-select:none}body .wrapper{display:flex;flex-direction:column;justify-content:space-between;height:100vh}.navHeader{display:flex;align-items:center;width:100%;height:4rem}.navHeader .hide{display:none}.navHeader .linkBlock{color:inherit;text-decoration:none}.navHeader .linkBlock span{position:relative;font-size:1rem;color:#2d2d2d;margin:1rem 0 1rem 2rem;top:30%}.navHeader .linkBlock:before{content:"←";position:absolute;margin:-0.4rem 0 0 0;color:#f1b926;font-size:1.5rem}.navHeader .searchBlock{display:flex;justify-content:center;width:40%;margin:0 auto;border-radius:.5rem;box-shadow:0 .2rem .3rem .3rem rgba(0,0,0,.1)}.navHeader .searchBlock .searchBtn{display:inherit}.navHeader .searchBlock input{width:80%;border:none;padding:0 1rem}.navHeader .searchBlock input:focus{outline:none}.navHeader .searchBlock img{width:1.5rem;height:1.5rem;padding:.3rem 0;cursor:pointer}.navHeader .searchBlock span{position:relative;font-size:1rem;color:#2d2d2d;margin:0 1rem;padding:.5rem 0;cursor:pointer}@media(max-width: 905px){.navHeader .searchBlock{width:50%}}@media(max-width: 600px){.navHeader .searchBlock{width:80%}}@media(max-width: 400px){.navHeader .searchBlock{width:90%}}