body{margin:0;color:#2d2d2d;font-family:"Roboto",sans-serif;font-size:16px;background-color:#fbfbfb;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;user-select:none}body .wrapper{display:flex;flex-direction:column;justify-content:space-between;height:100vh}.header{width:100%}.header .container{max-width:77rem;margin:0 auto;padding:0 1rem;display:flex;justify-content:space-between;align-items:center}.header img{width:5rem;height:5rem;object-fit:contain;padding:0}.header .catalogLink{width:8rem;height:2.5rem;color:inherit;text-decoration:none}.header .catalogLink:hover{cursor:pointer}.header button{width:100%;height:100%;font-size:1rem;color:#2d2d2d;border:.15rem solid #f1b926;border-radius:.4rem;background-color:#fff;position:relative;cursor:pointer}.header span{margin-left:1.3rem;padding:.2rem .5rem}.header span:before{content:"≡";position:absolute;left:.8rem;top:.3rem;font-size:1.3rem}.header .bg_yellow{background-color:#f1b926;cursor:default !important}