@import "../../../assets/index.scss";
.pagBtn, .currentBtn {
  width: 2rem;
  height: 2rem;
  margin: 0 0.1rem;
  padding: 0.2rem 0.2rem;
  border: none;
  border-radius: 0.3rem;
  background-color: $bg-fc;
  color: $black;
  cursor: pointer;
}
.pagDots, .arrowPag{
  width: 1rem;
  height: 1rem;
  margin: 0 0.5rem;
  text-align: center;
}
.currentBtn{
  @include currentOption;
  cursor: auto;
}
.arrowPag{
  cursor: pointer;
}

